@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Quicksand:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(45deg, rgb(37, 119, 21) 3.75%, #fff 3.75%, #fff 46.25%, rgb(37, 119, 21) 46.25%, rgb(37, 119, 21) 53.75%, #fff 53.75%, #fff 96.25%, rgb(37, 119, 21) 96.25%);
  background-size: 10px 10px;
  background-position: 50px 50px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    
}


.underline--magical {
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
  background-repeat: no-repeat;
  background-size: 100% 0.2em;
  background-position: 0 88%;
  transition: background-size 0.25s ease-in;
  
}


.underline--magical:hover {
  background-size: 100% 88%;
}
.App {
  text-align: center;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home-header {
    position :absolute;
    top: 10px;
}

.home-header h3 {
    font-family: 'Fredoka One', cursive;
    color: black;
    font-size: 4.5vw;
}

.home-btn {
    background-color: rgba(0,0,0,0.7);
    border-radius: 2rem;
    vertical-align: middle;
    align-self: center;
    box-shadow: 3px 3px 6px 1px rgba(0,0,0,0.5);
    -webkit-box-shadow: 3px 3px 6px 1px rgba(0,0,0,0.5);
    -moz-box-shadow: 3px 3px 6px 1px rgba(0,0,0,0.5);
}

.home-btn-text {
    color: white;
    display: flex;
    align-content: center;
    padding: 5px  !important;
   margin: 0;
}

.home-shadow {
    box-shadow: 7px 7px 6px 1px rgba(0,0,0,0.5);
    -webkit-box-shadow: 7px 7px 6px 1px rgba(0,0,0,0.5);
    -moz-box-shadow: 7px 7px 6px 1px rgba(0,0,0,0.5);
    
}

.home-carousel {
    width: 75%;
 }

@media only screen and (max-width: 759px) {
    .home-btn-text {
       font-size: 0.8rem;
    }   

    .home-carousel {
       width: 100%;
    }
    .home-header h3{
        font-size: 6.5vw;
    }


    }


   

