.cart-header {
   

}

.cart-header h3 {
    font-family: 'Fredoka One', cursive;
    color: black;
    font-size: 8vw;
    margin-bottom: 30px;
}



.cart-shadow {
    display: flex;
    border-radius: 5px;
    box-shadow: 0px 0px 6px 4px rgba(0,0,0,0.5);
    -webkit-box-shadow: 0px 0px 6px 4px rgba(0,0,0,0.5);
    -moz-box-shadow: 0px 0px 6px 4px rgba(0,0,0,0.5);
}
.card-cart {
    padding: 5px !important;
   
}

.cart {
    opacity: 0.95;
    background-color: white;
    border-radius: 5px;
    padding: 25px 5px;
    z-index: -1;
    margin-bottom: 20px;
    box-shadow: 7px 7px 6px 1px rgba(0,0,0,0.5);
    -webkit-box-shadow: 7px 7px 6px 1px rgba(0,0,0,0.5);
    -moz-box-shadow: 7px 7px 6px 1px rgba(0,0,0,0.5);
}

.items-in-cart-custom {
    display: flex;
    flex-direction: column;
    width: 90%;
    justify-content: space-around;
    flex-wrap: wrap;
    border-top: 1px gray solid;
    border-bottom: 1px gray solid;
    padding: 10px 0;
}

.total {
    margin: 20px 0px 10px 0; 
    display: flex;
    justify-content: space-between;
    border-bottom: 1px gray solid;
}

.total h4 {
   
    padding: 5px;
}

.cart-btn {
    background-color: rgba(0,0,0,0.7);
    border-radius: 2rem;
    vertical-align: middle;
    align-self: center;
}

.cart-btn-text {
    color: white;
    display: flex;
    align-content: center;
    padding: 15px  !important;
   margin: 0;
   font-size: 1.2rem;
}

.cart-form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 10px;
}

.cart-img {
    width: 8vh;
    height: 20vh;
    padding: 10px;
   
}


.cart-products {
    display: flex;
    margin: 5px;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    
}

@media only screen and (max-width: 759px) {
    .cart-header h3 {
        font-size: 12vw;
    }
}
